import { Injectable } from '@angular/core';
import { NativeDateAdapter } from '@angular/material/core';

/**
 * (d)d
 * @author SKK231527 
 */
@Injectable({
  providedIn: 'root',
})
export class JPDateAdapter extends NativeDateAdapter {
  getDateNames(): string[] {
    // * '1''31'
    return Array.from(Array(31), (value, key) => `${key + 1}`);
  }
}
